
import { defineComponent, ref } from "vue";
import type { FilterType, PFM202001SC00_REQ, PFM202001SC00_RES } from "@/shared/type/PFM202001SC00";
import pagination from "@/shared/components/Pagination/Pagination.vue";
import '@vuepic/vue-datepicker/dist/main.css'
import VueDatePicker from '@vuepic/vue-datepicker';
import { PAGE_SIZE } from "@/shared/constants/common.const";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

export default defineComponent({
    name: "pfm202001sc00",
    components: {
        pagination,
        BreadCrumb,
        VueDatePicker,
        SelectOption
    },
    data() {
        const headers = [
            { columnName: "No" },
            { columnName: "View" },
            { columnName: "Fee Code" },
            { columnName: "Apply Seq No" },
            { columnName: "Apply Start Date" },
            { columnName: "Apply End Date" },
            { columnName: "Fee Currency Code" },
            { columnName: "Fee Apply Status Code" },
            { columnName: "Base Fee Apply Start Amount" },
            { columnName: "Fee Min Amount" },
            { columnName: "Base Fee Amount" },
            { columnName: "Fee Max Amount" },
            { columnName: "Base Fee Rate%" },
        ];
        const searchTypeCode = ref<Array<FilterType>>([
            { key: '', value: 'All' },
            { key: '01', value: 'Product Fee Code' },
            { key: '02', value: 'Fee Apply Status Code' }
        ]);
        return {
            dataTable: [] as PFM202001SC00_RES[],
            headers,
            searchTypeCode,
            startIndex: 1 as number,
            totalCount: 0 as number,
            isResetPage: false as boolean,
            searchValue: {
                applyStartDate: '' as unknown,
                applyEndDate: '' as unknown,
                feeApplyStatusCode: '',
                productFeeCode: '',
                pageNumber: 1,
                pageSize: PAGE_SIZE
            }
        };
    },

    mounted() {
        this.requestFeeByTransaction(false);
    },

    computed: {
        formattedStartDate(): string {
            return this.searchValue.applyStartDate ? this.$datetime.formatVueDatePicker(this.searchValue.applyStartDate as Date) : ''
        },

        formattedEndDate(): string {
            return this.searchValue.applyEndDate ? this.$datetime.formatVueDatePicker(this.searchValue.applyEndDate as Date) : ''
        },
        covertCodeValue() {
            return (covertCodeType: string, baseCode: string) => `${baseCode}` + ' : ' + this.$codeConverter.convertValue(`${covertCodeType}`, `${baseCode}`);
        }
    },

    methods: {
        async requestFeeByTransaction(isSearch: boolean) {
            const body: PFM202001SC00_REQ = {
                productFeeCode: this.searchValue.productFeeCode,
                feeApplyStatusCode: this.searchValue.feeApplyStatusCode,
                applyStartDate: this.searchValue.applyStartDate ? this.searchValue.applyStartDate ? this.$datetime.convertDateToString(this.searchValue.applyStartDate as Date) : '' : '',
                applyEndDate: this.searchValue.applyEndDate ? this.searchValue.applyEndDate ? this.$datetime.convertDateToString(this.searchValue.applyEndDate as Date) : '' : '',
                pageSize: this.searchValue.pageSize,
                pageNumber: isSearch ? 0 : this.searchValue.pageNumber,
            };
            if (isSearch) {
                this.searchValue.pageNumber = 1
                this.isResetPage = true
            }
            await this.$request.getRequest('PFM20200111', body, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.dataTable = res?.body.list;
                        this.totalCount = res.body.totalCount;
                    }
                });
        },

        handlePagination(pageNumber: number) {
            this.isResetPage = false;
            this.searchValue.pageNumber = pageNumber;
            this.requestFeeByTransaction(false);
        },

        handleStartIndex(startIndex: number) {
            this.startIndex = startIndex;
        },

        handleClearSearch() {
            this.searchValue.applyStartDate = ''
            this.searchValue.applyEndDate = ''
            this.isResetPage = true;
            this.searchValue.productFeeCode = "";
            this.searchValue.pageNumber = 1;
            this.searchValue.feeApplyStatusCode = ''
            this.searchValue.pageSize = 10;
            this.requestFeeByTransaction(false);
        },

        handleClickAction(actionType: string, rowData?: PFM202001SC00_RES) {
            switch (actionType) {
                case "register":
                    this.$router.push({
                        path: `PFM202001SC01`,
                        query: { type: `register` }
                    });
                    break;
                case 'search':
                    this.requestFeeByTransaction(false);
                    break
                case 'clearSearch':
                    this.handleClearSearch();
                    break;
                case 'detail':
                    this.$router.push({
                        path: `PFM202001SC01`,
                        query: {
                            type: `edit`,
                            productFeeCode: rowData?.productFeeCode,
                            feeCurrencyCode: rowData?.feeCurrencyCode,
                            applyStartDate: rowData?.applyStartDate,
                            applySeqNo: rowData?.applySeqNo
                        }
                    });
                    break;
            }
        },
    },
});
