
import { defineComponent } from "vue";
import type { PFM201001SC00_REQ, PFM201001SC00_RES } from "@/shared/type/PFM201001SC00";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import '@vuepic/vue-datepicker/dist/main.css'
import VueDatePicker from '@vuepic/vue-datepicker';
import pagination from "@/shared/components/Pagination/Pagination.vue";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

export default defineComponent({
    name: "pfm201001sc00",
    components: {
        VueDatePicker,
        pagination,
        BreadCrumb,
        SelectOption
    },
    data() {
        const headers = [
            { label: 'No' },
            { label: 'View' },
            { label: 'Fee code' },
            { label: 'Fee Name' },
            { label: 'Fee Currency Code' },
            { label: 'Apply Start Date' },
            { label: 'Fee Start Time' },
            { label: 'Fee End Time' },
            { label: 'Apply End Date' },
            { label: 'Fee Apply Status Code' },
            { label: 'Product Fee Type Code' },
            { label: 'Product Fee Business Type Code' },
            { label: 'Online Manual Type Code' },
            { label: 'Fee Calc Method Type Code' },
            { label: 'Fee Description' }
        ];
        return {
            headers,
            dataTable: [] as PFM201001SC00_RES[],
            startIndex: 1 as number,
            totalCount: 0 as number,
            isResetPage: false as boolean,
            searchForm: {
                productFeeCode: "",
                feeCurrencyCode: "",
                productFeeName: "",
                applyStartDate: '' as unknown,
                applyEndDate: '' as unknown,
                feeApplyStatusCode: '',
                pageNumber: 1,
                pageSize: PAGE_SIZE
            }
        };
    },

    computed: {
        formattedStartDate(): string {
            return this.searchForm.applyStartDate ? this.$datetime.formatVueDatePicker(this.searchForm.applyStartDate as Date) : ''
        },

        formattedEndDate(): string {
            return this.searchForm.applyEndDate ? this.$datetime.formatVueDatePicker(this.searchForm.applyEndDate as Date) : ''
        },

        covertCodeValue() {
            return (covertCodeType: string, baseCode: string) => `${baseCode}` + ' : ' + this.$codeConverter.convertValue(`${covertCodeType}`, `${baseCode}`);
        }
    },

    mounted() {
        this.requestFeeBasicInfo(false);
    },

    methods: {
        async requestFeeBasicInfo(isSearch: boolean) {
            const body: PFM201001SC00_REQ = {
                productFeeCode: this.searchForm.productFeeCode.trim(),
                feeName: this.searchForm.productFeeName,
                feeCurrencyCode: this.searchForm.feeCurrencyCode,
                feeApplyStatusCode: this.searchForm.feeApplyStatusCode,
                applyStartDate: this.searchForm.applyStartDate  ? this.searchForm.applyStartDate ? this.$datetime.convertDateToString(this.searchForm.applyStartDate as Date) : '' : '',
                applyEndDate: this.searchForm.applyEndDate ? this.searchForm.applyEndDate ? this.$datetime.convertDateToString(this.searchForm.applyEndDate as Date) : '' : '',
                pageSize: this.searchForm.pageSize,
                pageNumber: isSearch ? 0 : this.searchForm.pageNumber,
            };
            if (isSearch) {
                this.searchForm.pageNumber = 1
                this.isResetPage = true
            }
            await this.$request.getRequest('PFM20100111', body, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.dataTable = res.body.list;
                        this.totalCount = res.body.totalCount;
                    }
                });
        },

        handlePagination(pageNumber: number) {
            this.isResetPage = false;
            this.searchForm.pageNumber = pageNumber;
            this.requestFeeBasicInfo(false);
        },

        handleStartIndex(startIndex: number) {
            this.startIndex = startIndex;
        },

        handleClearSearch() {
            this.searchForm.productFeeCode = "";
            this.searchForm.feeCurrencyCode = "";
            this.searchForm.pageNumber = 1;
            this.isResetPage = true;
            this.searchForm.feeApplyStatusCode = ''
            this.searchForm.pageSize = PAGE_SIZE;
            this.searchForm.applyStartDate = ''
            this.searchForm.applyEndDate = ''
            this.requestFeeBasicInfo(false);
        },

        handleClickAction(actionType: string, rowData: PFM201001SC00_RES) {

            switch (actionType) {
                case 'register':
                    this.$router.push({
                        path: `PFM201001SC01`,
                        query: { type: "register" }
                    });
                    break;
                case 'detail':
                    this.$router.push({
                        path: `PFM201001SC01`,
                        query: {
                            type: "edit",
                            productFeeCode: rowData.productFeeCode,
                            feeCurrencyCode: rowData.feeCurrencyCode,
                            applyStartDate: rowData.applyStartDate,
                            applySeqNo: rowData.applySeqNo
                        }
                    });
                    break;
                case 'search':
                    this.requestFeeBasicInfo(false);
                    break;
                case 'clearSearch':
                    this.handleClearSearch();
                    break;
            }
        },
    }
});
